import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import FavoriteContent from '../component/Favorite/FavoriteContent'

class Favorite extends Component {
  componentDidMount () {
    const {setDataLoaded, getComicList} = this.props

    setDataLoaded(false)
    getComicList({isFavorite: true})
  }

  render () {
    return (
      <main>
        <FavoriteContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    comicList: state.comicList,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getComicList: (query) => dispatch(actionCreators.getComicList(query)),
    updateComicItem: (comic, query) => dispatch(actionCreators.updateComicItem(comic, query)),
    toggleLocalFavorite: (comic) => dispatch(actionCreators.toggleLocalFavorite(comic)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setSelectComic: (selectComic) => dispatch(actionCreators.setSelectComic(selectComic))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Favorite)



